import { Component, ViewChild } from '@angular/core';
import { LoadingController,ViewController,IonicPage,  NavController,Events , ActionSheetController, NavParams } from 'ionic-angular';
import {   FormBuilder,  FormGroup,   Validators } from '@angular/forms';
import { Auth,NotificationProvider } from '../../../providers';
import { TranslateService } from '@ngx-translate/core';
import { Storage } from '@ionic/storage';


@IonicPage()
@Component({
  selector: 'page-chat-homecreate',
  templateUrl: 'chat-homecreate.html',
})
export class ChatHomecreatePage {
  @ViewChild('fileInput') fileInput;
  public form                  : FormGroup;
  items: any=[];
  tiposus: any=[];
  public nombre		     : any;
  public cuitt		     : any;
  public estado		     : any;
  public nota		     : any;
  public evento		     : any;
  username: string = "";
  public cui		     : any;
  public idsus2		     : any;
  public idnombre2		     : any;
  idempresa:any;
  private _ID                  : String;
  private _IDSUSCRIPTOR                  : String;
  public image              	: any;
  public pageTitle           	: string;
  public idsuscriptor                : any;
  public idcomprasaldo                : any;
  public vtarifa  :any=[];

  isDisablednit :any;
  constructor(public storage: Storage, private loadingCtrl: LoadingController,public msj: NotificationProvider, public translateService: TranslateService,public viewCtrl: ViewController,public authService:  Auth,public navCtrl 		: NavController,  
    public navParams 	: NavParams,   private _FB          : FormBuilder,         public actionSheetCtrl: ActionSheetController, 
          public events: Events)
  {
    this.translateService.get('APPX').subscribe((value) => {
      this.idempresa=value
      this.authService.getReg('5c2d5aea1008753130026280' + '/' + this.idempresa,'dcatalogos').then((result) => {
        this.vtarifa=result;
      }, (err) => {  
      this.msj.showError3('Error',err.error);
    });
     
  })
    
     this.form = this._FB.group({
        cui        	    : ['', Validators.required],
        estado     : ['', Validators.required],
        nota     : ['', Validators.required],
     });

     
    

  }

  ngOnInit() {
    this.storage.get('username').then((value) => {
      this.username = value;
  });
  
  this.storage.get('tiposus').then((value) => {
    this.tiposus = value.split(',');
  this.cuitt=this.tiposus[2]
  this.cui=this.tiposus[2]
  this.idsus2=this.tiposus[3]
  this.idnombre2=this.tiposus[4]
    if(this.tiposus[0]=='Distribuidor')
          {
            this.isDisablednit=true;
          }
          else
          {
            this.isDisablednit=false;
          }
          this.focused('a')

});

  }
  ionViewDidLoad() : void
  {// this._IDSUSCRIPTOR=this.navParams.data.record2.idsuscriptor;
    this.pageTitle="Compra de saldo";
    if(this.navParams.get("record")==null)
    {
       
     
      
      this.isDisablednit =true
    }
    else {
      
       this._ID 				=	this.navParams.data.record._id;
       this.idsuscriptor 				=	this.navParams.data.record.idsuscriptor._id;
       this._IDSUSCRIPTOR				=	this.navParams.data.record.idsuscriptor._id;
     
       this.estado		=	this.navParams.data.record.monto;
       this.nota			=	this.navParams.data.record.nota;
       this.isDisablednit =false
     

    }
  }

  manageGallery() : void
  {

    let creat='Actualiza';
    if(this.navParams.get("record")==null)
    {  creat='Crea';
    }

     let   options       : any	     = { 
     idsuscriptor : {id:this._IDSUSCRIPTOR,nombre:this.nombre} ,
     nombre:this.nombre,
     estado : this.form.controls['estado'].value,
     nota : this.form.controls['nota'].value,
     bitacora:{idempresa:this.idempresa,idafiliado:'',email: this.username,permiso:creat,accion:creat+' reverion '}
    };
      

     if(this.navParams.get("record")==null)
     {  
          if(options){
            this.authService.createReg('',options,'userchats').then((result) => {
              if (!this.form.valid) { return; }
              this.viewCtrl.dismiss(result);
          }, (err) => {
            this.msj.showError3('Error',err.error);
          });
          }
     }   
     else
     {
      if(options){
        this.authService.createReg(this._ID,options,'userchats').then((result) => {
           if (!this.form.valid) { return; }
           this.viewCtrl.dismiss(result);
       }, (err) => {
        this.msj.showError3('Error',err.error);
       });
    }}
  }

  cancel() {
    this.viewCtrl.dismiss();
  }
  done() {
    if (!this.form.valid) { return; }
    //this.viewCtrl.dismiss(this.form.value);
  }




  focused(selectedValue: any) {
    if(( this.cui) && (this.cui!=''))
    {   

     const loading = this.loadingCtrl.create({
       spinner: 'bubbles',
       content: "Espere un momento,  Buscando información ......"
     });
     loading.present();


     this.items=[]
     this.authService.getReg(this.cui+'/'+'persona/' + this.idempresa,'personals').then((data) => {
      // this.msj.showError3('Resultado','Proceso OV concluido, revisa tus datos para validar información ');
       this.nombre=data[0].nombre
       this.idsuscriptor 				=	data[0]._id;
       this._IDSUSCRIPTOR				=	data[0]._id;
     
       this.items=data;
       loading.dismiss();
     
   }, (err) => {


 this.msj.showError3('Resultado','No CUI invalido ');
 this.cui=''
 this.items=[]
 loading.dismiss();
 

    
    
   });

    }
     
   }

}



